const colors = [
  '#FFFFFF',
  '#2196F3',
  '#4CAF50',
  '#FF9800',
  '#009688',
  '#795548',
];

const refs = {
  start: document.querySelector('button[data-action="start"]'),
  stop: document.querySelector('button[data-action="stop"]'),
  body: document.querySelector('body'),
};

const randomIntegerFromInterval = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
};

let interval = null;

refs.start.addEventListener('click', changeBGHandler);
refs.stop.addEventListener('click', clearBGHandler);

function changeBGHandler() {
  if (!interval) {
    interval = setInterval(() => {
      refs.body.style.background =
        colors[randomIntegerFromInterval(0, colors.length - 1)];
    }, 1000);
  }
}

function clearBGHandler() {
  clearInterval(interval);
  interval = null;
  refs.body.removeAttribute('style');
}
